@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{box-sizing:border-box}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.app{margin:0;padding:0}

.front{width:100%;height:100vh;background-image:url(/jackson-christmas-card-2021/static/media/pittsburghBackground.e228118e.jpg);background-size:cover;background-position:center;display:flex;justify-content:center;align-items:flex-start;position:relative}.text-container{margin-bottom:4rem;display:flex;flex-direction:column;justify-content:center;align-items:center;padding:2.5rem 1rem}h2,p{text-align:center;padding:0}h2{margin:0;color:white;font-weight:500;font-size:2.25rem}p{color:white;margin-top:0.5rem;margin-bottom:1.5rem;font-size:1rem}.arrows-down{position:absolute;bottom:0.5rem;left:0;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.icon-down--small{color:white;font-size:2.25rem;margin-bottom:-1.25rem}.icon-down--small:hover{cursor:pointer}.icon-down--large{color:white;font-size:3rem}.icon-down--large:hover{cursor:pointer}@media (min-width: 1024px){.front{justify-content:flex-end;align-items:flex-end}.text-container{margin-bottom:0;align-items:flex-end;padding:0 1rem}h2{font-size:3rem}}

.back{width:100%;background-color:#650404;display:flex;flex-direction:column;position:relative}.holidayImage{background-image:url(/jackson-christmas-card-2021/static/media/familyLove.a897db75.jpg);background-size:cover;background-position:center;width:80%;display:none}.holidayMobileImage{background-image:url(/jackson-christmas-card-2021/static/media/familyLove.a897db75.jpg);background-size:cover;background-position:bottom;width:18rem;height:22rem;margin:1rem 0;border-radius:0.5rem;box-shadow:rgba(0,0,0,0.35) 0px 5px 15px}.holidayMessage{width:100%;padding:1rem;display:flex;flex-direction:column;align-items:center}.holidayMessage h2{text-align:center;margin:0;padding:0;padding-bottom:1rem;font-size:3.25rem;font-weight:600;font-family:"Dancing Script", cursive}.holidayMessage p{color:white;text-align:left;font-size:1rem;line-height:1.75rem;padding:0 1rem}@media (min-width: 768px){.holidayMobileImage{width:30rem;height:35rem}}@media (min-width: 1024px){.back{flex-direction:row;overflow:hidden;height:100vh}.holidayImage{width:45%;display:block}.holidayMobileImage{display:none}.holidayMessage{width:55%;padding:2rem 1rem 2rem 2rem}.holidayMessage h2{text-align:left}.holidayMessage p{font-size:0.875rem;line-height:1.5rem;padding:0}}@media (min-width: 1200px){.holidayMessage p{flex-grow:1;font-size:1rem;line-height:1.75rem;margin:1rem 0}}

.eventWrapper{display:flex;justify-content:space-between;width:100%}.yearEvents{display:flex;flex-direction:column;flex-wrap:wrap;width:85%;padding:2rem 1rem 5rem 2rem}.yearEvents h2{text-align:left;font-size:1.125rem;font-weight:600;margin-bottom:0.5rem;color:#f8b229}.yearEvents p{text-align:left;font-size:1rem;line-height:1.5rem;color:#e5e5e5}.images{display:flex;justify-content:flex-start;flex-wrap:wrap;margin:0.5rem 0}.thumbnail{border-radius:0.25rem;padding:0.5rem;height:7rem;margin:0 0.5rem 0.5rem 0}.thumbnail:hover{border:0.125rem solid #ddd;cursor:pointer}.christmasLights{width:15%;display:flex;align-items:stretch;justify-content:center}.christmasLights .vl{border-right:0.125rem solid #e5e5e5;display:flex;flex-direction:column;justify-content:center}.christmasLights li{width:2rem;height:2rem;text-align:center;line-height:2rem;border-radius:1rem;background:#161515;border:2px solid #e5e5e5;margin:0 -1rem 0 0.5rem;display:inline-block;position:relative}.christmasLights li::before{content:"";position:absolute;top:0.9rem;left:-4rem;width:4rem;height:0.2rem;background:#e5e5e5;z-index:-1}.christmasLights .active{border:none}.christmasLights .active.red{box-shadow:rgba(187,37,40,0.5) 0 0 15px 8px;background:#bb2528}.christmasLights .active.yellow{box-shadow:rgba(248,178,41,0.5) 0 0 15px 8px;background:#f8b229}.christmasLights .active.green{box-shadow:rgba(20,107,58,0.5) 0 0 15px 8px;background:#146b3a}@media (min-width: 768px){.thumbnail{height:12rem}.yearEvents{padding:4rem 0rem 4rem 0rem;margin:0 4rem 0rem 6rem;border-bottom:0.088rem solid silver}.christmasLights .vl{justify-content:flex-start}.christmasLights li{margin:8rem -1rem 0rem 0.5rem}}

.timeline{background-color:#161515;color:#e5e5e5;width:100%;display:flex}.events{flex-grow:1;display:flex;flex-direction:column;align-items:flex-end}

